/* Elements */
.button-green-large, .button-green-small, .button-blue-large, .button-white-large {
  display: inline-block;
  vertical-align: middle;
  background: #35a45b;
  color: #fff;
  font-size: 14px;
  text-decoration: none;
  font-weight: 700;
  height: 34px !important;
  line-height: 34px !important;
  padding: 0 15px;
  border-radius: 30px;
  border: 1px solid #35a45b;
  position: relative;
  text-transform: lowercase;
}

.button-green-large:disabled, .button-green-small:disabled, .button-blue-large:disabled, .button-white-large:disabled {
  background: #c4c4c4 !important;
  border-color: #c4c4c4 !important;
}

.button-green-small {
  color: #35a45a !important;
  border: 1px solid #35a45a !important;
  font-weight: 400 !important;
}

.button-review-more {
  border: 1px solid #acacac !important;
  color: #818181 !important;
  font-weight: 400 !important;
}

.button-green-small {
  height: auto;
  font-weight: 500;
}

.button-blue-large {
  background: #3a6699;
}

.button-white-large {
  background: transparent;
  color: #818181;
  font-weight: 500;
  line-height: 2.6em;
  border: 1px solid #acacac;
  position: relative;
  text-transform: none;
}

.input-text,
.textarea,
.select {
  border: 1px solid #acacac;
  border-radius: 0.313em;
  font-weight: 400;
  font-size: 1em;
  height: 2.7em;
  box-sizing: border-box;
  padding: 0 0 0 0.625em;
  vertical-align: middle;
  width: 100%;
}

.textarea {
  line-height: normal;
  padding-top: 0.625em;
  overflow: auto;
  height: 6.250em;
}

.form-row {
  padding: 0 0 1.4em 0;
  position: relative;
}

.select {
  background: white;
}

.select__arrow {
  position: relative;
  cursor: pointer;
  padding-right: 2em;
}

.select__arrow:before {
  content: '';
  position: absolute;
  right: .5em;
  top: .9em;
  width: 1.2em;
  height: 1.2em;
  background: transparent url(/img/svg/dropdown-gray.svg) no-repeat;
  background-size: 1.2em;
  box-sizing: border-box;
}

.select .selected-items {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

.multiselect-popup {
  padding: 0 0 20px 0;
}

.multiselect-list {
  margin: 0;
  padding: 0 0 3em 0;
  list-style: none;
  cursor: pointer;
}

.multiselect-list-item {
  position: relative;
  text-align: left;
  padding: 0.8em 4em 0.8em 1.2em;
  border-bottom: 0.063em solid #dcdcdc;
}

.multiselect-list-item:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 1.1em;
  width: 1.750em;
  height: 1.375em;
  margin-top: -0.6875em;
  background: url("/img/icons/bullet.png") no-repeat;
  background-size: contain;
  opacity: 0.2;
}

.multiselect-list-item__active:after {
  opacity: 1;
}

.badge {
  background: #35a45b;
  color: #fff;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  display: inline-block;
  border-radius: 100%;
  padding: 0.47em;
  min-width: 1.15em;
}

.button-phone {
  background: #35a45b;
  border-radius: 100%;
  box-sizing: border-box;
  width: 3.2em;
  height: 3.2em;
  padding: .6em;
  display: inline-block;
}

.button-phone:after {
  content: '';
  display: block;
  background: url("/img/icons/phone.svg") no-repeat 0 0.063em;
  width: 100%;
  height: 100%;
}
